.App {
  width: 87%;
  margin: auto;
  font-size: xx-large;
  color: white;
}

.App-header {
  height: 16vh;
  min-height: 120px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: xx-large;
  color: white;
}

.App-header h1 {
  margin-left: 2vmin;
  font-weight: 600;
}

.App-logo {
  animation: App-logo-spin infinite 2s linear;
  height: 8vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

